// * {
//     box-sizing: border-box;
// }

body, html {
    margin: 0;
    height: 100%;
}

section {
    height: 100%;
}

main {
    height: 100%;
}
#start-btn {
    position: absolute;
    opacity: 0;
    display: inline-block;
    margin: auto;
    margin-top: 10vh;
    padding: 10px;
    font-size: 2em;
    background-color: white;
    border: 2px solid black;
    transition: background 0.5s ease-in-out, color 0.5s ease-in-out, opacity 1s ease-in-out;
    text-decoration: none;
    color: black;
    right: -50vw;
}

.link-container {
    overflow: hidden;
    position: relative;
}

#start-btn:hover{
    background-color: lightgrey;
}

#title {
    text-align: center;
    vertical-align: middle;
    font-size: 5em;
    padding-top: 10vh;
}

.home {
    height: 100vh;
    text-align: center;
    font-family: Cloiser;
    background-image: url(https://cdn.clipart.email/d4637513f23dee0b46e952f6e690dc45_fire-breathing-dragons-pictures-clip-art-library_1500-864.gif);
    background-size: 100% 100%;
    background-color: rgba(255, 255, 255, 0.7);
    background-blend-mode: lighten;
}

.class-label {
    font-size: 2em;
    padding: 10px;
    border: 1px solid #CCC;
    cursor: pointer;
    z-index: 90;
}


#character-select input[type='button']{
    padding: 10px;
    font-size: 2em;
    font-family: Cloiser;
    background-color: white;
    border: 2px solid black;
    transition: background 0.5s ease-in-out, color 0.5s ease-in-out;

    &:hover {
        background-color: lightgrey;
    }

    &#checked {
        background-color: black;
        color: white;
    }
}

div.button {
    display: inline-block;
    text-align: center;
    position: relative;
    padding: 10px;
    font-size: 1em;
    z-index: 2;
    background-color: black;
    font-family: "Times New Roman", Times, serif;
    border: 2px solid white;
    color: white;
    opacity: 0.5;
    transition: background 0.5s ease-in-out, color 0.5s ease-in-out;
    vertical-align: middle;
    &:hover {
        background-color: grey;
        cursor: pointer;
    }
}

.button-disabled {
    display: inline-block;
    position: relative;
    text-align: center;
    padding: 10px;
    font-size: 1em;
    z-index: 2;
    background-color: black;
    font-family: "Times New Roman", Times, serif;
    border: 2px solid white;
    color: white;
    opacity: 0.5;
    pointer-events: none;
}


#stoke-fire {
    margin-top: 15%;
}

.battle-options-container {
    bottom: 50px;
    display: flex;
    align-items: flex-end;
    position: absolute;
    width: 100%;
    flex-wrap: nowrap;
    justify-content: center;
    height: auto;
}

#attack-btn {
    order: 1;
    flex: 1 1 auto;
    margin: 0px 30px;
    padding: 20px;
}

#alt-btn{ 
    order: 2;
    flex: 1 1 auto;
    margin: 0px 30px;
    padding: 20px;
}

.cooldown {
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: -1;
    height: 100%;
    background-color: #DDDDDD;
    text-align: center;
}

#choose-class {
    font-size: 2em;
    font-weight: 700;
}



.game {
    top: 0%;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(to bottom, rgba(255,255,255,0) 50%, #272823 50%);
    background-size: 100% 200%;
    color: white;
    overflow: auto;
}


.title, .game {
    position: absolute;
    width:100%;
}

.interaction-container {
    height: 650px;
    border: 1px solid transparent;
    transition: border 2s ease-in-out;
    position: relative;
}

.text-container {
    width: 300px;
    height: 350px;
    top: 10px;
    left: 0px;
    // border: 1px solid rgba(white, 0.6);
    position: absolute;
    overflow: hidden;
}

.items-container {
    width: 300px;
    height: 300px;
    // border: 1px solid white;
    top: 350px;
    left: 0px;
    position: absolute;
}

.wrapper {
    margin: auto;
    width: 800px;
    position:relative;
    padding: 10px 0 0 320px;
    // border: 1px solid red;
    transition: opacity 2s ease-in-out;
}

.flame{
    height: 100px;
    position: relative;
    margin-top: 22%;
    border-radius: 50%;
    opacity: 0;
    z-index: 1.5;
    transition: opacity 1s ease-in-out, box-shadow 2s ease-in-out;
    
    box-shadow:
        inset 0 0 60px 20px rgba($color: red, $alpha: 0.4),  /* inner white */
        0 0 60px 40px rgba($color: red, $alpha: 0.4);  /* inner white */
}

#flame-sm{
    animation: flame-sm 2s linear infinite alternate;
    opacity: 0.2;
}

#flame-md{
    animation: flame-md 2s linear infinite alternate;
    opacity: 0.4;
}

#flame-lg{
    animation: flame-lg 2s linear infinite alternate;
    opacity: 0.6;
}

#flame-xl{
    animation: flame-xl 2s linear infinite alternate;
    opacity: 0.8
}

.flame-border {
    background-image: radial-gradient(red, blue, orange);
    
}


#intro-btn {
    overflow: hidden;
    position: relative;
    // transition: opacity 2s ease-in-out;
}

.notification {
    margin-bottom: 10px;
    opacity: 0;
    transition: opacity 1s ease-in-out;
}

.text-gradient {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100%;
    width: 100%;
    background: linear-gradient( rgba(39, 40, 35, 0) 0%, rgba(39, 40, 35, 1) 100% );
}




.enemy {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}


@keyframes flame-sm{

    0% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.15),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.15);  /* inner white */
      }
    50% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.2),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.2);  /* inner white */
      }
    100% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.15),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.15);  /* inner white */
      }
}

@keyframes flame-md{

    0% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.15),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.15);  /* inner white */
      }
    50% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.3),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.2);  /* inner white */
      }
    100% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.25),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.25);  /* inner white */
      }
}

@keyframes flame-lg{

    0% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.15),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.15);  /* inner white */
      }
    50% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.32),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.32);  /* inner white */
      }
    100% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.30),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.30);  /* inner white */
      }
}


@keyframes flame-xl{

    0% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.15),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: .15);  /* inner white */
      }
    50% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: 0.34),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.34);  /* inner white */
      }
    100% {    box-shadow:
      inset 0 0 60px 20px rgba($color: red, $alpha: .32),  /* inner white */
      0 0 60px 40px rgba($color: red, $alpha: 0.32);  /* inner white */
      }
}


@font-face {
    font-family: Cloiser;
    src: url('fonts/CloisterBlack.ttf') format("truetype");
}
